import { ReactNode } from 'react';
import NextLink from 'next/link';
import Button from './Button.styles';

const Link = ({
  children,
  href,
  button = false,
}: {
  children: ReactNode;
  href: string;
  button?: boolean;
}) => {
  if (button) {
    return (
      <NextLink href={href}>
        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
        <a>
          <Button role={'link'}>{children}</Button>
        </a>
      </NextLink>
    );
  }
  return (
    <NextLink href={href}>
      {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
      <a>{children}</a>
    </NextLink>
  );
};

export default Link;
